import React, {useEffect, useState} from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import CircularProgress from '@material-ui/core/CircularProgress';
import {Link, useParams} from 'react-router-dom';
import IntlMessages from 'util/IntlMessages';
import AttachFile from '@material-ui/icons/AttachFile';
import axios from "axios";
import {base_url} from "../config";
import {useHistory} from 'react-router-dom';
import Fab from '@material-ui/core/Fab';

const CompanyUserSignUp = (props) => {
    const History = useHistory();
    const params = useParams();
    const [first_name, setFirstName] = useState("");
    const [last_name, setLastName] = useState("");
    const [job_title, setJobTitle] = useState("");
    const [mobile, setPhoneNumber] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [profile_pic, setProfilePic] = useState(null);
    const [ProfilePicUrl, setProfilePicUrl] = useState('https://via.placeholder.com/300x300')
    const [register_flag, setRegisterFlag] = useState(0)
    
    useEffect(() => {
        axios.post(base_url + "user/checkValidate", {invite_code: params.invite_code})
        .then((response) => {
            if (response.data.status==="success") {
                setFirstName(response.data.user.first_name)
                setEmail(response.data.user.email)
                //setJobTitle(response.data.user.user_type)
                setRegisterFlag(1)
            }
            else {
                History.push("/")
            }
        })
        .catch((error)=> error)
        
    }, []);
    const registerUser = ()=>{
        const userData = new FormData();
        userData.append('email', email);
        userData.append('password', password);
        userData.append('first_name', first_name);
        userData.append('last_name', last_name);
        userData.append('job_title', job_title);
        userData.append('mobile', mobile);
        userData.append('profile_pic', profile_pic);
        axios.post(base_url + "companyuser/register", userData)
        .then((response) => {
            if (response.data.status==="success") {
                History.push("/")
            }
            else {
                NotificationManager.error("You must input data in the field!");
            }
        })
    }
    const changeUserImg = (e) =>{
        e.preventDefault();
        let reader = new FileReader();
        let file = e.target.files[0];
        reader.onloadend = () => {
            setProfilePic(file);
            setProfilePicUrl(reader.result);
        }
        reader.readAsDataURL(file)
    }

    return (
        
        <div className="app-login-container d-flex justify-content-center align-items-center animated slideInUpTiny animation-duration-3">
            {register_flag?(
            <div className="app-login-main-content">
                <div className="app-logo-content d-flex align-items-center justify-content-center">
                    <Link className="logo-lg" to="/" title="Jambo">
                        <img src={require("assets/images/logo.png")} alt="sirvez" title="sirvez"/>
                    </Link>
                </div>

                <div className="app-login-content">
                    <div className="app-login-header">
                        <h1>Company User Sign Up</h1>
                    </div>

                    <div className="app-login-form">
                        <form method="post" action="/">
                            <div>
                                <div className="col-xl-6 col-lg-6 col-md-6 col-6 m-auto">
                                    <div className="jr-card-thumb">
                                        <img className="card-img-top img-fluid" alt="products" src={ProfilePicUrl}/>
                                    </div>
                                    <input type="file" id="profile_img" accept="image/*" 
                                            name="profile_img" style={{display: "none"}}
                                            onChange={(event) => changeUserImg(event)}/>

                                    <div className="jr-card-social text-right">
                                        <Button className="jr-btn text-primary">
                                            User Profile
                                        </Button>
                                        <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" 
                                            onClick={(e)=>{document.getElementById('profile_img').click()}}>
                                            <i className="zmdi zmdi-cloud-upload zmdi-hc-2x"/>
                                        </Fab>
                                    </div>
                                </div>
                                <TextField
                                    type="text"
                                    label="First Name"
                                    onChange={(event) => setFirstName(event.target.value)}
                                    fullWidth
                                    defaultValue={first_name}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Last Name"
                                    onChange={(event) => setLastName(event.target.value)}
                                    fullWidth
                                    defaultValue={last_name}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Job Title"
                                    onChange={(event) => setJobTitle(event.target.value)}
                                    fullWidth
                                    defaultValue={job_title}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="text"
                                    label="Phone Number"
                                    onChange={(event) => setPhoneNumber(event.target.value)}
                                    fullWidth
                                    defaultValue={mobile}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                />

                                <TextField
                                    type="email"
                                    onChange={(event) => setEmail(event.target.value)}
                                    label={<IntlMessages id="appModule.email"/>}
                                    fullWidth
                                    defaultValue={email}
                                    margin="normal"
                                    className="mt-0 mb-2"
                                    disabled
                                />

                                <TextField
                                    type="password"
                                    onChange={(event) => setPassword(event.target.value)}
                                    label={<IntlMessages id="appModule.password"/>}
                                    fullWidth
                                    defaultValue={password}
                                    margin="normal"
                                    className="mt-0 mb-4"
                                />

                                {/* <div className="mb-3 d-flex align-items-center justify-content-between">
                                    <input type="file" id="profile_img" accept="image/*" 
                                        name="profile_img" style={{display: "none"}}
                                        onChange={(event) => setProfilePic(event.target.files[0])}/>
                                    <Button className="attach-file jr-btn text-muted" 
                                        variant="contained" onClick={(e)=>{document.getElementById('profile_img').click()}}>
                                        <AttachFile/> Profile Picture
                                    </Button>
                                  
                                </div> */}
                                <div className="mb-3 d-flex align-items-center justify-content-between">
                                    <Button variant="contained" onClick={() => {
                                        registerUser()
                                    }} color="primary">
                                        <IntlMessages
                                            id="appModule.regsiter"/>
                                    </Button>
                                    <Link to="/signin">
                                        <IntlMessages id="signUp.alreadyMember"/>
                                    </Link>
                                </div>

                            </div>

                        </form>
                    </div>
                </div>
            </div>
            )
            :
            (
                <div></div>
            )
            }

           
            <NotificationContainer/>
        </div>
    )
};


export default CompanyUserSignUp;
