import axios from "axios";
const base_url = "https://app.sirvez.com/api/";
const public_url = "https://app.sirvez.com/";
const sticker_url = "https://app.sirvez.com/pixie/assets/images/stickers/";
const $http = axios.create({
    baseURL: base_url,
    timeout: 20000,
    // headers: {
    //     "Content-Type": "application/json; multipart/form-data"
    // }
})

$http.interceptors.request.use((request) => {
    if (localStorage.getItem("token"))
        request.headers = {
            "Content-Type": "application/json; multipart/form-data",
            'X-Auth-Token': localStorage.getItem("token")
        }
    request.mode = 'cors'
    return request;

});

$http.interceptors.response.use(
    function(response) {
        if (response.data.status === "TokenError") {
            //perform the manipulation here and change the response object
            //alert("You are not authorized");
            localStorage.removeItem('user_id');
        }
        return response;
    },
    function(error) {
        if (!navigator.onLine)
            return Promise.reject("You are offline. Your request will send when online.");
        else
            return Promise.reject(error.message)

    }
);


const config = {
    base_url: base_url
}
export default config;
export { base_url, public_url, sticker_url, $http }